export function checkIdentityId (rule,value,callback) {
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
  if(!value){
    return callback(new Error(''));
  } else if(reg.test(value)){
    return callback(new Error(''));
  } else {
    callback();
  }
}

//,
export function notCheckIdentityId (rule,value,callback) {
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
  if(!value){
    callback();
    return;
  } else if(reg.test(value)){
    return callback(new Error(''));
  } else {
    callback();
  }
}


