export function checkInterNum (rule,value,callback) {
//  const reg = /^[0-9]*[1-9][0-9]*$/;
  const  reg = /^(0|[1-9][0-9]*|-[1-9][0-9]*)$/;
  if(!value){
    return callback(new Error(''));
  }else if(!reg.test(value)){
    return callback(new Error(''));
  }else{
    callback();
  }
}

// ,
export function notCheckInterNum (rule,value,callback) {
  const  reg = /^(0|[1-9][0-9]*|-[1-9][0-9]*)$/;
  if(!value){
    callback();
    return;
  }else if(!reg.test(value)){
    return callback(new Error(''));
  }else{
    callback();
  }
}

// 
export function checkNumber (rule,value,callback) {
  const  reg = /^-?\d+(\.\d+)?$/;
  if(!value){
    return callback(new Error(''));
  }else if(!reg.test(value)){
    return callback(new Error(''));
  }else{
    callback();
  }
}

// ,
export function notCheckNumber (rule,value,callback) {
  const  reg = /^-?\d+(\.\d+)?$/;
  if(!value){
    callback();
    return;
  }else if(!reg.test(value)){
    return callback(new Error(''));
  }else{
    callback();
  }
}


export default {
  QQ:[{required: true,validator:QQV,trigger:'change'}],   //QQ
  notQQ:[{required: false,validator:notQQV,trigger:'change'}],   //QQ,

  phone:[{required: true, pattern: /^1[34578]\d{9}$/, message:'',trigger: 'change'}], //
  notPhone:[{required: false, pattern: /^1[34578]\d{9}$/, message:'',trigger: 'change'}], //,

  checkMoney:[{required: true,validator:checkMoney,trigger:'change'}], //
  notCheckMoney:[{required: false,validator:notCheckMoney,trigger:'change'}], //,

  checkId:[{required: true,validator:checkIdentityId,trigger:'change'}], //
  notCheckId:[{required: false,validator:notCheckIdentityId,trigger:'change'}], //,

  checkInt:[{required: true,validator:checkInterNum,trigger:'change'}] , //
  notCheckInt:[{required: false,validator:notCheckInterNum,trigger:'change'}] , //,

  checkNum:[{required: true,validator:checkNumber,trigger:'change'}] , //
  notcheckNum:[{required: false,validator:notCheckNumber,trigger:'change'}] , //,
  //
  checkLength: function (tmin,tmax) {
    return [{ required: true,min:tmin, max:tmax, message: ''+tmin+''+tmax+'!', trigger: 'change' }]
  },
  //
  maxlength: function (max) {
    return [{ required: true,min:1, max:max, message: ''+max+'!', trigger: 'change' }]
  },
  //
  checkMustFill:[{ required: true, message: '!', trigger: 'change',whitespace: true }],
  //
  checkSelectValue:[{ required: true, message: '!', trigger: 'change',whitespace: true }],
  //
  checkSelectArray:[{ required: true, type: "array", message: '!', trigger: 'change',whitespace: true }],
}



