<template>
    <el-table-column v-bind="column" >
      <template v-for="item in getChildren">
        <xyj-multilevel-header v-if="item.children && item.children.length" :key='item.arrange' :column="item"></xyj-multilevel-header>
        <el-table-column v-else :key="item.arrange" v-bind="item" sortable = "custom"></el-table-column>
      </template>
    </el-table-column>
</template>

<script>
  export default {
    name: 'xyjMultilevelHeader',
    props: {
      column: {
        type: Object,
        default(){
          return {
            resizable: true,// el-table  border 
            minWidth: '10px',//
            showOerflowTooltip: true, // tooltip
            headerAlign: 'center',//
            children: [] //
          }
        }
      }
    },
    computed:{
      getChildren(){
        return this.column.children.sort((a,b) => {
          return a.arrange-b.arrange
        })
      }
    }
  }
</script>

<style scoped>

</style>



